const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

   @property(cc.Node)
   nGame: cc.Node = null;
   
   @property(cc.Node)
   nHome: cc.Node = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }
    onClickPlay() {
       this.nHome.active = false;
       this.nGame.active = true; 
    }
    // update (dt) {}
}
